      flex-wrap: wrap;
      align-items: center;

      &--heading {
        width: 792px;
        padding-top: 16px;
        padding-bottom: 16px;
        font-weight: 600;

        & > .second-lvl-container__text {
          cursor: pointer;

          & .sorting-icon {
            margin-left: 4px;
            transform: translateY(1px);
          }

          & .sorting-icon--sort-down path:first-child { //down arrow
            fill: #936DFF;
            stroke: #936DFF;
          }

          & .sorting-icon--sort-up path:last-child { //up arrow
            fill: #936DFF;
            stroke: #936DFF;
          }
        }

      }

      &--regular {
        padding-top: 10px;
        padding-bottom: 10px;

        & > p {
          &:last-of-type {
            margin-right: 12px;
          }
        }

        &.foldable {
          & > p {
            &:last-of-type {
              position: relative;
              cursor: pointer;

              &::after {
                content: "";
                display: block;
                position: absolute;
                right: 0;
                bottom: 6px;
                background-image: url(../images/icons/icon_down.svg);
                background-repeat: no-repeat;
                height: 7px;
                width: 11px;
              }

              &:hover {
                color: #D96DFF;

                &::after {
                  background-image: url(../images/icons/icon_down_hover.svg);
                }
              }

            }
          }

          &--unfold {
            & > p:last-of-type::after {
              background-image: url(../images/icons/icon_up.svg);
            }

            & > p:last-of-type:hover {
              color: #D96DFF;

              &::after {
                background-image: url(../images/icons/icon_up_hover.svg);
              }
            }


            & .second-lvl-container__hidden-part {
              display: block;
            }
          }
        }
      }

      &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        overflow-y: scroll;
        height: 420px;

        padding-right: 12px;

        // scrollbar stuff
        scrollbar-color: #D4D4F4 rgba(212, 212, 244, 0.1);
        scrollbar-width: thin;
        scrollbar-gutter: stable;

        &::-webkit-scrollbar-thumb {
          background: #D4D4F4;
          border-radius: 8px;
          width: 6px;
        }

        &::-webkit-scrollbar {
          background: rgba(212, 212, 244, 0.1);
          border-radius: 8px;
          width: 6px;
        }

        // scrollbar stuff

      }

      & > p {
        margin: 0;

        &:nth-child(1) {
          flex-basis: 374px;
          margin-right: 32px;
        }
        &:nth-child(2) {
          flex-basis: 346px;
        }


        & > a {
          color: #936DFF;
        }
        & > a:active {
          color: #936DFF;
        }
        & > a:visited {
          color: #936DFF;
        }
        & > a:hover {
          color: #D96DFF;
        }
      }
    }
  }
}

